/**
 * Base SCSS
 */


html {font-size:16px;}

h1, h2, h3, h4, h5, h6 {
  @include heading;
}
h1 {color:$red;font-size:4rem;}
h2 {font-size:3rem;}
h3 {font-size:2.5rem;}
h4 {font-size:2rem;}
h5 {font-size:1.5rem;}
h6 {font-size:1rem;font-weight:600;}

p {margin-bottom:25px;}

a:hover {
  color: $red-link;
}

body {
  min-height: 2000px;
  padding-top: 70px;
  font-family:'PT Sans Narrow', sans-serif;
  font-size:2rem;
  color:$grey-medium;
}

.row {padding: 7rem 0px;}

.vertical-align {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

#curtain-slider {
  height:800px;
}

.round {
  border-radius:50%;
  border:1px solid $blue;
}

.search-dropdown, .sub-dropdown {
	display:block;
  width: 100%;
  padding:0 100px;
  background: rgba(255,255,255, .9);
  z-index:999999999999!important;
  position:fixed;
  top:0!important;
  border-top:2px solid #d1d1d1;
  border-bottom:2px solid #d1d1d1;
}
